html,body{margin:0;position:relative;color:#ffffff;font-family:'Squada One', cursive;overflow-x:hidden;overflow-y:hidden}html div.container,body div.container{background-color:black;height:38px;min-width:270px;position:absolute;top:0;right:-20px;transform:skewX(45deg)}html div.container .time-word,body div.container .time-word{color:white;font-size:36px;position:absolute;left:30px;transform:skewX(-45deg)}html div.container div.inner-container,body div.container div.inner-container{background-color:black;height:54px;min-width:160px;position:absolute;right:10px;transform:none}html div.container div.inner-container .stopwatch,body div.container div.inner-container .stopwatch{background-color:#000000;display:inline-block;font-size:50px;position:relative;right:-30px;margin-left:10px;margin-right:10px;transform:skewX(-45deg)}
